<template>
    <div class="flex-user">
        <div
            style="
                background-color: #fff;
                margin-bottom: 10px;
                padding: 12px 24px;
                box-sizing: border-box;
                border-radius: 4px;
            "
        >
            <a-form :form="form" layout="vertical">
                <a-row :gutter="24">
                    <a-col :span="4">
                        <a-form-item label="模型名称">
                            <a-input
                                placeholder="请输入模型名称"
                                v-model="form.modelName"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item label="设计者">
                            <a-input
                                placeholder="请输入设计者"
                                v-model="form.designer"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item label="审核者">
                            <a-input
                                placeholder="请输入审核者"
                                v-model="form.reviewer"
                            ></a-input>
                        </a-form-item>
                    </a-col>

                    <a-col :span="4">
                        <a-form-item :label="'\u00a0'">
                            <g-btn
                                text="查询"
                                class="mr-2"
                                type="primary"
                            ></g-btn>
                            <g-btn text="重置"></g-btn>
                        </a-form-item>
                    </a-col>
                </a-row>
            </a-form>
        </div>

        <div
            style="
                background-color: #fff;
                padding: 12px 24px 0;
                box-sizing: border-box;
                overflow-y: hidden;
                border-radius: 4px;
            "
        >
            <!-- <div
        style="display: flex; justify-content: flex-end; margin-bottom: 10px"
      >
        
      </div> -->
        <g-table-bar :spacing.sync="tableSize" :columns.sync="columns" :virtualColumns="virtualColumns" @refreshTable="refresh">
                <g-btn
                    text="添加"
                    style="margin-right: 10px"
                    @callback="addOrEditSpec"
                ></g-btn>
                <g-btn
                    text="批量删除"
                    type="danger"
                    style="margin-right: 10px"
                ></g-btn>
                <g-btn text="导出excel" style="margin-right: 10px"></g-btn>
            </g-table-bar>
            <a-table
                id="tables"
                :columns="columns"
                :data-source="dataList"
                :scroll="{ y: tableheight }"
                :row-key="(record) => record.id"
                :pagination="pagination"
                :rowSelection="rowSelection"
                @change="tableChange"
                :loading="isTableLoading"
                :size="tableSize"
            >
                <!-- 角色 -->
                <span slot="role" slot-scope="text">
                    <a-tag color="blue">{{ text }}</a-tag>
                </span>
                <!-- 操作 -->
                <span slot="action" slot-scope="text, record">
                    <a
                        href="javascript:;"
                        class="mr-1"
                        @click="addOrEditSpec(record)"
                        >编辑</a
                    >
                    <a href="javascript:;" class="del" @click="delSpec(record)"
                        >删除</a
                    >
                </span>
            </a-table>
        </div>
    </div>
</template>

<script>
import { getTableScroll } from "@/utils/getTableScroll";
import createModal from "@/utils/modal";
// import CreateOrEditSpecConfig from "./create-or-edit-spec-config/create-or-edit-spec-config.vue";
import { tableBase } from "@/utils";
export default {
    name: "model-design-infoTrace",
    mixins: [tableBase],
    data() {
        return {
            tableheight: 0,
            form: {
                modelName: "",
                designer: "",
                reviewer: "",
            },
        };
    },

    mounted() {


        this.columns = [
            // 序号 index
            {
                title: "序号",
                dataIndex: "id",
                key: "id",
                customRender: (text, record, index) => `${index + 1}`,
                width: 80,
            },
            {
                title: "模型名称",
                dataIndex: "modelName",
                key: "modelName",
            },
            {
                title: "模型版本",
                dataIndex: "modelVersion",
                key: "modelVersion",
            },
            {
                title: "设计者",
                dataIndex: "designer",
                key: "designer",
            },
            {
                title: "设计时间",
                dataIndex: "designTime",
                key: "designTime",
                sorter: true,
                ellipsis: true,
                width: 200,
            },
            {
                title: "审核者",
                dataIndex: "reviewer",
                key: "reviewer",
            },
            {
                title: "审核时间",
                dataIndex: "reviewTime",
                key: "reviewTime",
                sorter: true,
                ellipsis: true,
                width: 200,
            },
            {
                title: "变更描述",
                dataIndex: "changeDescription",
                key: "changeDescription",
                ellipsis: true,
            },

            {
                title: "操作",
                dataIndex: "action",
                key: "action",
                scopedSlots: { customRender: "action" },
            },
        ];
        //this.columns数组深拷贝this.virtualColumns
        this.virtualColumns = JSON.parse(JSON.stringify(this.columns)).map(
            (item) => {
                item.checked = true;
                return item;
            }
        )
        this.$nextTick(() => {
            this.tableheight = getTableScroll({ id: "tables" });
        });

    },

    methods: {
        addOrEditSpec(record) {
            createModal(
                {
                    title: record ? "编辑建模设计信息" : "添加建模设计信息",
                    width: "800px",
                },
                { component: CreateOrEditSpecConfig }
            );
        },

        fetchData(request) {
            // 手动循环生成数据 200条
            this.isTableLoading = true;
            setTimeout(() => {
                var _data = [];
                for (let i = 0; i < 200; i++) {
                    _data[i] = {
                        id: i,
                        modelName: "模型" + i,
                        modelVersion: "V1.0",
                        designer: "设计者" + i,
                        designTime: "2022-01-01 00:00:00",
                        reviewer: "审核者" + i,
                        reviewTime: "2022-01-02 00:00:00",
                        changeDescription: "变更描述" + i
                    };
                }
                request.finishedCallback && request.finishedCallback();
                request.successCallback &&
                    request.successCallback({ items: _data, totalCount: 200 });
            }, 200);
        },

        delSpec(record) {
            // 删除 延时
            this.$confirm({
                title: "确认删除",
                content: "确认删除吗?",
                onOk: () => {
                    this.isTableLoading = true;
                    setTimeout(() => {
                        // 刷新
                        this.refresh();
                    }, 500);
                },
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.flex-user {
    box-sizing: border-box;
}
</style>